import React from 'react';
import { addToCartActionCreator } from '../../../../../redux/productsReducer';
import AddToCart from './AddToCart';
import ProductsContext from '../../ProductsContext';

export default function AddToCartContainer(props) {
    const { dispatch } = React.useContext(ProductsContext);

    let addToCart = () => dispatch(addToCartActionCreator(props.item.id, props.item.qty));
    
    return <AddToCart addToCart={addToCart} />
}