import React, { useContext } from 'react';
import { onChangeToBuyActionCreator } from '../../../../../redux/productsReducer';
import InputQty from './InputQty';
import ProductsContext from '../../ProductsContext';

export default function PriceContent(props) {
    const {dispatch} = useContext(ProductsContext);
    
    let onChange = event =>
        dispatch(onChangeToBuyActionCreator(props.item.id, event.target.value))

    return <InputQty onChangeQty={onChange} qty={props.item.qty} />
}